
import Capa from './Components/Capa';
import ComoFunciona from './Components/ComoFunciona';
import Header from './Components/Header';
import Footer from './Components/Footer';
import Portifolio from './Components/Portifolio'
import './Style/App.css';
import { useState} from 'react';
import Orcameto from './Components/Orcameto.jsx';
import DadosUser from './Components/DadosUser';

function App() {
  
  const[alternar, setAlternar] = useState(true)
  const[areas, setAreas] = useState(true)

  const exibir = (props)=>{
    setAlternar(props)
  }
  const scrolling = ()=>{  
    let altura = window.screen.height*.75;
    window.scrollTo(0, altura)
    console.log(altura)    
  }
  const PassarAreas = (props)=>{  
   console.log(props)
   setAreas(props)
  }

  return (
    <div className="App" >
    <Header exibir={exibir}/>
    
    {alternar &&
      <>
      <Capa scrolling={scrolling}/>
      <ComoFunciona/>
      <DadosUser PassarAreas={PassarAreas}/>      
      <Orcameto areas={areas}/>
      </>
    }
    {!alternar &&
      <>
      <Portifolio />
      </>
    }
    <Footer/> 
    
    </div>
    );
  }
  
  export default App;
  