import React from 'react';
import '../Style/DadosUser.css'
import { useState , useEffect} from 'react';
const DadosUser = ({PassarAreas}) =>{ 
    
    const areas =[
        {name:'Quarto' , pequeno:'9' , ideal:'12', grande:'20'},
        {name:'Suite' , pequeno:'12' , ideal:'13.7', grande:'25'},
        {name:'Cozinha' , pequeno:'5' , ideal:'7.5', grande:'12'},
        {name:'Sala de estar' , pequeno:'15' , ideal:'12', grande:'30'},
        {name:'Sala de jantar' , pequeno:'12' , ideal:'12', grande:'30'},
        {name:'Banheiro Social' , pequeno:'2.4' , ideal:'3.5', grande:'5'},
        {name:'Area de serviço' , pequeno:'3' , ideal:'4', grande:'6'},
        {name:'Varanda coberta' , pequeno:'3' , ideal:'6', grande:'20'},
    ]
    
    const initialValue = {  
        larguraTerreno: '0',
        comprimentoTerreno: '0',
        nPavimentos:'1',
        nApartamentos:'1',
    }
    for(let i = 0;i<areas.length;i++){
        let dado1 = 't'+areas[i].name.replace(' ','').replace(' ','').replace('ç','c')
        let dado2 = 'n'+areas[i].name.replace(' ','').replace(' ','').replace('ç','c')
        initialValue[dado1] = areas[i].ideal
        initialValue[dado2] = '0'
    }    
    const[dadoForm,setDadoForm] = useState(initialValue)
    const[areaTotal,setareaTotal] = useState(0)
    const[areaDeTerreno,setareaDeTerreno] = useState(0)
    const form = (e)=>{
        const {name, value} = e.target
        if(value == ''){ setDadoForm({ ...dadoForm, [name]: '0'}   )}
        else{            setDadoForm({ ...dadoForm, [name]: value} )}
    }
    //Calculo de Areas
    useEffect(() => {
        let areaDaCasa = 0 ;
        let valorTotal = 0
        areas.forEach(x=>{
            let dado1 = 't'+x.name.replace(' ','').replace(' ','').replace('ç','c')
            let dado2 = 'n'+x.name.replace(' ','').replace(' ','').replace('ç','c')        
            let tamanho = parseFloat(dadoForm[dado1])
            let quantidade = parseInt(dadoForm[dado2])
            areaDaCasa = areaDaCasa + tamanho * quantidade     
        })
        let a = (parseInt(dadoForm.nPavimentos) * parseInt(dadoForm.nApartamentos) * (areaDaCasa)).toFixed(2)
        let b = (parseInt(dadoForm.larguraTerreno) * parseInt(dadoForm.comprimentoTerreno)).toFixed(2)
        setareaTotal(a)
        setareaDeTerreno(b)
    });
    //-------
    return(
        <div className='DadosUser'>
        <div className='card--conteiner'>
        <h1>Como deseja</h1>
        <div className='card'>
        <div className='Dados'>
        <div className='item'>
        <h4>Área do terreno:</h4>
        <div className='item--input'>
        <input type='number' placeholder='12m' min='0' name={'larguraTerreno'} onChange={form}/>
        <h5>x</h5>
        <input type='number' placeholder='20m' min='0' name={'comprimentoTerreno'} onChange={form}/> 
        </div>
        </div>                        
        <div className='item'>
        <h4>Numero de pavimentos:</h4> 
        <input type='number' placeholder='und' min='0' max='5' name={'nPavimentos'} onChange={form}/>
        </div>
        <div className='item'>
        <h4>Apartamentos por Andar:</h4> 
        <input type='number' placeholder='und' min='0' name={'nApartamentos'} onChange={form}/>
        </div>
        </div>
        
        <div className='Dados'>        
        {areas.map((x,i)=>{
            let name1 = 'n'+x.name.replace(' ','').replace(' ','').replace('ç','c')
            let name2 = 't'+x.name.replace(' ','').replace(' ','').replace('ç','c')
            return(
                <div className='item' key={i}>
                <h4>{x.name}:</h4>
                <div className='item--input'>
                <input type='number' placeholder='und' min='0' max='9' name={name1} onChange={form}/>-
                <select name={name2} onChange={form}>
                <option value={x.pequeno}>Pequeno</option>                      
                <option selected='selected' value={x.ideal}>Ideal</option>                
                <option value={x.grande}>Grande</option>
                </select>
                </div>
                </div>
                )})}
               
                </div>
                </div>
                <div className='title'>
                <h4>Area do terreno: {areaDeTerreno}m²</h4>
                <h4>Area a construir: {areaTotal}m²</h4>
                </div>
                <button type='button' onClick={()=>{ PassarAreas({areaTotal,areaDeTerreno}) }}>Orçar</button>
                </div>
                </div>
                )    
            }
            export default DadosUser;